// NOTE
// Do not write styles in this less file.
// This should only contain less-specific,
// functional elements. I.e., nothing in
// here should print out as actual styles.

// color

$black:    #000000;
$gray_50:  #FAFAFA;
$gray_100: #F5F5F5;
$gray_200: #EEEEEE;
$gray_300: #E0E0E0;
$gray_400: #BDBDBD;
$gray_500: #9E9E9E;
$gray_600: #757575;
$gray_700: #616161;
$gray_800: #424242;
$gray_900: #212121;
$white:    #ffffff;

$red:    #FD5750;
$orange: #FD8650;
$yellow: #FDE550;
$green:  #97FC4F;
$cyan:   #6CFCAF;
$blue:   #6DB6FC;
$purple: #9A57fC;
$pink:   #FD57E1;

$background: $black;
$primary: $white;
$focus: #069BFA;

// type

$font_sans: 'Roboto', sans-serif;
$font_mono: 'Roboto Mono', monospace;
$font_slab: 'Roboto Slab', serif;

$font_weight: normal;
$font_size: 16px;

$font_tiny: 13px;
$font_height: 1.5;
$font_color: $gray_800;
$font_family: $font_sans;

$font: $font_weight #{$font_size}/$font_height $font_family;
$code: $font_weight #{$font_tiny}/20px $font_mono;

// time

$now: 0.0ms;
$run: 250ms;
$run_faster: ($run / 10.0);
$run_fast:   ($run / 2.50);
$run_slow:   ($run * 1.50);
$run_slower: ($run * 2.00);
$sooner: $run_faster;
$soon:   $run_fast;
$late:   $run_slow;
$later:  $run_slower;

// timing

$fadein: cubic-bezier(0.445, 0.05, 0.55, 0.95);
$bounce: cubic-bezier(.55, 0, .55, 1.44);
$deform: cubic-bezier(0.39, 0.575, 0.565, 1);

// size

$tile:     (1rem  * 4);
$tile_xl:  ($tile * $tile);
$tile_xxl: ($tile * $tile * $tile);
$tile_xxs: ($tile / $tile / $tile);
$tile_xs:  ($tile / $tile);

$padding: ($tile_xs * $font_height) + rem;
$margin: ($tile_xs * $font_height) + rem;

$z_index_up: 1;
$z_index_down: -1;

$border_width: 1px;
$border_style: solid;
$border_color: $gray_300;
$border_radius: $tile_xxs;
$border: $border_width $border_style $border_color;

$outline_width: 2px;
$outline_style: solid;
$outline_color: $focus;
$outline_offset: 1px;
$outline: $outline_color $outline_style $outline_width;

@mixin m2048 {
  @media screen and (max-width: (2048 - 1px)) {
    @content;
  }
}

@mixin m1600 {
  @media screen and (max-width: (1600 - 1px)) {
    @content;
  }
}

@mixin m1280 {
  @media screen and (max-width: (1280 - 1px)) {
    @content;
  }
}

@mixin m1024 {
  @media screen and (max-width: (1024 - 1px)) {
    @content;
  }
}

@mixin m768 {
  @media screen and (max-width: (768 - 1px)) {
    @content;
  }
}

@mixin s512 {
  @media screen and (max-width: (512 - 1px)) {
    @content;
  }
}

