@import 'common.scss';

html {
  color: $white;
  font: $font;
  text-size-adjust: 100%;
  font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparentize($black, 1);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  overflow: hidden;
}

.link {
  color: $white;
  text-decoration: none;
  &:hover {
    color: $gray_200;
  }
}

.app {
  background-color: $background;
  min-height: 100vh;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./bkg.jpg');
  background-size: cover;
  background-position: center;
  animation: ($run_slower * 1.5) $now $fadein both image;
}

.section {
  display: flex;
}

.article {
  font-size: 20px;
  line-height: 30px;
  display: block;
  flex: 0 1 25em;
  margin: 15vw 15vw 0 15vw;
  .link {
    margin-left: -2px;
    padding: 0 2px;
    background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    box-shadow: 0 -1px rgba(255, 255, 255, 0) inset;
    transition: background $run_fast $deform, box-shadow $run_fast $fadein;
    &:hover {
      color: $white;
      background-size: 100% 1px;
      box-shadow: 0 -1px $gray_800 inset;
    }
  }
  h1 {
    font-size: 3em;
    line-height: 1;
    font-weight: 300;
    margin: 0 0 .5rem;
    margin-left: -0.08em;
    animation: $run_slower $run $fadein both snippet;
  }
  p {
    margin: 0 0 .5em;
    animation: $run_slower ($run + $run_faster) $fadein both snippet;
  }
}

.storytime {
  display: flex;
  align-items: flex-end;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-image: linear-gradient(rgba(0,0,0,0) 75%, rgba(0,0,0,1));
}

.storytime-wrapper {
  position: relative;
  width: 100%;
  margin: $tile 0;
}

.timeline {
  display: flex;
  align-items: center;
  animation: $run_slower $now $fadein backwards slip_in_b2t;
}

.flex-line {
  flex: 1;
  height: 2px;
  background: darken(white, 80%);
  background: $gray_800;
}

.time {
  color: $gray_800;
  font: $code;
  margin: 0 .25rem;
}

.storyline {
  //
}

.story {
  line-height: 1;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  transform-origin: left;
  transform: rotate3d(0, 0, 1, -30deg);
}

.story-dash {
  width: $tile;
  height: 2px;
  margin-left: -2px;
  background: $gray_800;
}

.story-title {
  white-space: nowrap;
}

.story-link {
  padding: 0.25rem 1rem;
  border: 2px solid $gray_800;
  border-radius: $tile;
  &:hover {
    border-color: white;
  }
}

@keyframes fade_in {
  0% { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slip_in_b2t {
  0% { opacity: 0; transform: translate(0, $tile); }
  to { opacity: 1; transform: translate(0); }
}

@keyframes image {
  0% { opacity: 0; transform: translate3d(0, 1%, 0); }
  to { opacity: 1; transform: none; }
}

@keyframes snippet {
  0% { opacity: 0; transform: translate3d(0, 10%, 0); }
  to { opacity: 1; transform: none; }
}

@include animate_stories;
